___CSS_LOADER_EXPORT___.push([module.id, "\n#product-input[data-v-07ef36d1] {\n    width: 50%;\n}\n#button-container[data-v-07ef36d1] {\n    text-align: end;\n    vertical-align: bottom;\n}\n#previous-button[data-v-07ef36d1] {\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium)\n        var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #2f3160;\n    opacity: 1;\n    font-weight: bold;\n}\n.default-button[data-v-07ef36d1] {\n    background: #2f3160 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    opacity: 1;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium)\n        var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--white);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #ffffff;\n    opacity: 1;\n    margin-left: 10px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/deploySettings.vue"],"names":[],"mappings":";AAqYA;IACI,UAAU;AACd;AACA;IACI,eAAe;IACf,sBAAsB;AAC1B;AACA;IACI,kBAAkB;IAClB;;yCAEqC;IACrC,kDAAkD;IAClD,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;IACV,iBAAiB;AACrB;AACA;IACI,+CAA+C;IAC/C,kBAAkB;IAClB,UAAU;IACV,kBAAkB;IAClB;;yCAEqC;IACrC,kDAAkD;IAClD,mBAAmB;IACnB,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;IACV,iBAAiB;AACrB","sourcesContent":["<script setup lang=\"ts\">\nimport { onBeforeMount, ref } from \"vue\";\nimport { container } from \"tsyringe\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport {\n    IModule,\n    IModuleSettingsItem,\n    IApplicationSettingsItem,\n} from \"../../../../domain/model/models\";\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { IGitProject } from \"../../../../domain/infrastructure/IProviderApi\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IDebounce } from \"../../../../domain/infrastructure/IDebounce\";\nimport { IApplicationSettings } from \"../../../../domain/useCase/IApplicationSettings\";\nimport { INotification } from \"../../../../domain/presentation/INotification\";\n\nonBeforeMount(() => {\n    fillDatatableRow();\n    getGitGroupsFromProvider();\n    getProductsFromProvider();\n});\n\ninterface IProduct {\n    products: Array<IGitProject>;\n    loaded: boolean;\n}\n\ninterface IGroup {\n    value: IGitProject;\n    microservicesCache: Array<IGitProject>;\n    requestedBefore: boolean;\n}\n\ninterface IGitGroup {\n    groups: Array<IGroup>;\n    loaded: boolean;\n}\n\ninterface IDataTableRow {\n    moduleID: IModule[\"ID\"];\n    moduleName: IModule[\"name\"];\n    gitGroupName: string;\n    gitGroupID: number;\n    microservices: Array<IGitProject>;\n    microservicesDisable: boolean;\n    microserviceName: string;\n    microserviceID: number;\n}\n\nconst emit = defineEmits([\"close\"]);\n\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst notification = container.resolve<INotification>(INotification);\nconst appSettingsUseCase = container.resolve<IApplicationSettings>(IApplicationSettings);\nconst debouncer = container.resolve<IDebounce>(IDebounce);\n\nconst gitProducts = ref({ products: [], loaded: false } as IProduct);\nconst gitGroups = ref({ groups: [], loaded: false } as IGitGroup);\nconst currentApp = ref(viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\nconst selectedProduct = ref({ ID: 0, name: \"\" });\nconst dataTableRows = ref(Array<IDataTableRow>());\n\nconst appDatatableColumns = ref([\"moduleName\", \"gitGroup\", \"microservice\"]);\nconst optionsDataTable = ref({\n    filterable: false,\n    perPage: 5,\n    perPageValues: [],\n    headings: {\n        name: localization.get(\"moduleName\"),\n        gitGroup: localization.get(\"gitGroup\"),\n        microservice: localization.get(\"microservice\"),\n    },\n    sortable: [],\n    texts: {\n        count: \"\",\n    },\n});\n\nfunction fillDatatableRow() {\n    dataTableRows.value = viewModel.studio.items\n        .filter((item) => item.objectType === \"module\")\n        .map<IDataTableRow>((item) => {\n            const module: IDataTableRow = {\n                moduleID: item.ID,\n                moduleName: item.name,\n                gitGroupName: \"\",\n                gitGroupID: (item as IModule).deployParameters?.gitGroupId || 0,\n                microservices: [],\n                microservicesDisable: true,\n                microserviceName: \"\",\n                microserviceID: (item as IModule).deployParameters?.gitServiceRepoId || 0,\n            };\n            return module;\n        });\n}\n\nfunction getProductsFromProvider() {\n    appSettingsUseCase\n        .getProjectsOfProduct()\n        .then((data) => {\n            gitProducts.value.products = data;\n            gitProducts.value.loaded = true;\n            setSelectedProduct();\n        })\n        .catch(() => {\n            notification.showNotification({ text: \"Error while fetching products\", type: \"error\" });\n        });\n}\n\nfunction setSelectedProduct() {\n    if (currentApp.value) {\n        if (currentApp.value.deployParameters) {\n            const currentProduct = gitProducts.value.products.find(\n                (product) => currentApp.value!.deployParameters!.gitProductRepoId == product.id\n            );\n            if (currentProduct) {\n                selectedProduct.value = {\n                    name: currentProduct.name,\n                    ID: currentProduct.id,\n                };\n            }\n        }\n    }\n}\n\nasync function getGitGroupsFromProvider() {\n    appSettingsUseCase\n        .getGroupNamesWithId()\n        .then((data) => {\n            data.forEach((fetchedGroup) =>\n                gitGroups.value.groups.push({\n                    value: fetchedGroup,\n                    microservicesCache: [],\n                    requestedBefore: false,\n                })\n            );\n            gitGroups.value.loaded = true;\n            setSelectedGitGroups();\n        })\n        .catch(() =>\n            notification.showNotification({\n                text: \"Error while fetching git groups\",\n                type: \"error\",\n            })\n        );\n}\n\nasync function setSelectedGitGroups() {\n    dataTableRows.value.forEach((row) => {\n        if (row.gitGroupID !== 0) {\n            const group = gitGroups.value.groups.find((group) => group.value.id == row.gitGroupID);\n            if (group) {\n                row.gitGroupName = group.value.name;\n            } else {\n                row.gitGroupID = 0;\n            }\n        }\n    });\n    setSelectedMicroservices();\n}\n\nasync function setSelectedMicroservices() {\n    const groupIdList: Array<number> = dataTableRows.value\n        .map((row) => row.gitGroupID)\n        .filter((id, index, self) => id !== 0 && self.indexOf(id) === index);\n\n    groupIdList.forEach((id) => {\n        appSettingsUseCase\n            .getProjectsByGroupId(id)\n            .then((data) => {\n                const group = gitGroups.value.groups.find((group) => group.value.id == id);\n                if (group) {\n                    group.microservicesCache = data;\n                    group.requestedBefore = true;\n                    dataTableRows.value.forEach((row) => {\n                        if (row.gitGroupID === id) {\n                            row.microservices = data;\n                            if (row.microserviceID !== 0) {\n                                const microservice = data.find(\n                                    (service) => service.id == row.microserviceID\n                                );\n                                if (microservice) {\n                                    row.microserviceName = microservice.name;\n                                    row.microservicesDisable = false;\n                                } else {\n                                    row.microserviceID = 0;\n                                    row.microservicesDisable = true;\n                                }\n                            }\n                        }\n                    });\n                }\n            })\n            .catch(() =>\n                notification.showNotification({\n                    text: \"Error while fetching projects\",\n                    type: \"error\",\n                })\n            );\n    });\n}\n\nfunction handleProductSelect() {\n    const product = gitProducts.value.products.find(\n        (product) => product.name === selectedProduct.value.name\n    );\n    if (product) {\n        selectedProduct.value.ID = product.id;\n    } else {\n        selectedProduct.value.name = \"\";\n        selectedProduct.value.ID = 0;\n    }\n}\n\nasync function handleGitGroupInput(selectedRow: IDataTableRow) {\n    debouncer.resetTimer();\n    const selectedGitGroup = gitGroups.value.groups.find(\n        (gitGroup) => gitGroup.value.name === selectedRow.gitGroupName\n    );\n    const currentRow = dataTableRows.value.find((row) => row.moduleID == selectedRow.moduleID)!;\n    currentRow.microservicesDisable = true;\n    currentRow.microservices = [];\n    currentRow.microserviceName = \"\";\n    currentRow.microserviceID = 0;\n\n    if (selectedGitGroup) {\n        currentRow.gitGroupID = selectedGitGroup.value.id;\n        currentRow.gitGroupName = selectedGitGroup.value.name;\n\n        if (!selectedGitGroup.requestedBefore) {\n            debouncer.debounce(getMicroservicesByGroupID, 500, selectedGitGroup, currentRow);\n        } else {\n            currentRow.microservices = selectedGitGroup.microservicesCache;\n            currentRow.microservicesDisable = !currentRow.microservices.length;\n        }\n    } else {\n        currentRow.gitGroupName = selectedRow.gitGroupName;\n        currentRow.gitGroupID = 0;\n    }\n}\n\nasync function getMicroservicesByGroupID(selectedGitGroup: IGroup, currentRow: IDataTableRow) {\n    currentRow.microservices = await appSettingsUseCase.getProjectsByGroupId(\n        selectedGitGroup.value.id\n    );\n    selectedGitGroup.microservicesCache = currentRow.microservices;\n    selectedGitGroup.requestedBefore = true;\n    currentRow.microservicesDisable = !currentRow.microservices.length;\n}\n\nasync function handleGitGroupChange(selectedRow: IDataTableRow) {\n    const selectedGitGroup = gitGroups.value.groups.find(\n        (gitGroup) => gitGroup.value.name === selectedRow.gitGroupName\n    );\n    const currentRow = dataTableRows.value.find((row) => row.moduleID == selectedRow.moduleID)!;\n    currentRow.gitGroupID = selectedGitGroup?.value?.id || 0;\n    currentRow.gitGroupName = selectedGitGroup?.value?.name || \"\";\n    if (!currentRow.gitGroupID) {\n        currentRow.microservices = [];\n        currentRow.microservicesDisable = true;\n        currentRow.microserviceName = \"\";\n        currentRow.microserviceID = 0;\n    }\n}\n\nfunction handleMicroServiceChange(selectedRow: IDataTableRow) {\n    const selectedMicroservice = selectedRow.microservices.find(\n        (microservice) => microservice.name === selectedRow.microserviceName\n    );\n    const currentRow = dataTableRows.value.find((row) => row.moduleID == selectedRow.moduleID)!;\n    currentRow.microserviceName = selectedMicroservice?.name || \"\";\n    currentRow.microserviceID = selectedMicroservice?.id || 0;\n}\n\nasync function onSave() {\n    if (currentApp.value) {\n        let settings = {};\n        const modulesSettings: Array<IModuleSettingsItem> = dataTableRows.value\n            .filter(\n                (row) =>\n                    row.microserviceID !=\n                    (viewModel.studio.items.find((item) => item.ID == row.moduleID) as IModule)\n                        .deployParameters?.gitServiceRepoId\n            )\n            .map((row) => ({\n                moduleID: row.moduleID,\n                deployParameters: {\n                    gitGroupId: row.gitGroupID,\n                    gitServiceRepoId: row.microserviceID,\n                },\n            }));\n\n        const applicationSettings: IApplicationSettingsItem = {\n            applicationID: currentApp.value!.ID,\n            deployParameters: {\n                gitProductRepoId: selectedProduct.value.ID,\n            },\n        };\n\n        settings = { modulesSettings };\n\n        if (currentApp.value.deployParameters?.gitProductRepoId != selectedProduct.value.ID) {\n            settings = { ...settings, applicationSettings };\n        }\n\n        await executor.execute(\n            async () => {\n                await appSettingsUseCase.update(settings);\n            },\n            { loading: true }\n        );\n    }\n}\n</script>\n\n<template>\n    <h1>Deploy Settings</h1>\n    <label for=\"product-input\">Select Product</label>\n    <input\n        class=\"form-control\"\n        list=\"products\"\n        id=\"product-input\"\n        placeholder=\"Select Product\"\n        v-model=\"selectedProduct.name\"\n        @change=\"handleProductSelect\"\n        :disabled=\"!gitProducts.loaded\"\n    />\n    <datalist id=\"products\">\n        <option value=\"\" disabled selected>Select Product for Application</option>\n        <option v-for=\"product in gitProducts.products\">\n            {{ product.name }}\n        </option>\n    </datalist>\n    <br />\n    <br />\n    <v-client-table\n        ref=\"table\"\n        :data=\"dataTableRows\"\n        :columns=\"appDatatableColumns\"\n        :options=\"optionsDataTable\"\n    >\n        <template #moduleName=\"props\">\n            <span>{{ props.row.moduleName }}</span>\n        </template>\n        <template #gitGroup=\"props\">\n            <input\n                class=\"form-control\"\n                list=\"git-groups\"\n                id=\"git-group-input\"\n                placeholder=\"Select Git Group\"\n                v-model=\"props.row.gitGroupName\"\n                @input=\"handleGitGroupInput(props.row)\"\n                @change=\"handleGitGroupChange(props.row)\"\n                :disabled=\"!gitGroups.loaded\"\n            />\n            <datalist id=\"git-groups\">\n                <option v-for=\"gitGroup in gitGroups.groups\" :value=\"gitGroup.value.name\"></option>\n            </datalist>\n        </template>\n        <template #microservice=\"props\">\n            <input\n                class=\"form-control\"\n                :list=\"props.row.moduleID\"\n                id=\"microservice-input\"\n                placeholder=\"Select Microservice\"\n                v-model=\"props.row.microserviceName\"\n                @change=\"handleMicroServiceChange(props.row)\"\n                :disabled=\"props.row.microservicesDisable\"\n            />\n            <datalist :id=\"props.row.moduleID\">\n                <option\n                    v-for=\"microservice in props.row.microservices\"\n                    :value=\"microservice.name\"\n                ></option>\n            </datalist>\n        </template>\n    </v-client-table>\n    <hr />\n    <div id=\"button-container\">\n        <span id=\"previous-button\" @click=\"() => emit('close')\">{{\n            localization.get(\"cancel\")\n        }}</span>\n        <button class=\"default-button\" @click=\"onSave\">{{ localization.get(\"save\") }}</button>\n    </div>\n</template>\n\n<style scoped>\n#product-input {\n    width: 50%;\n}\n#button-container {\n    text-align: end;\n    vertical-align: bottom;\n}\n#previous-button {\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium)\n        var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #2f3160;\n    opacity: 1;\n    font-weight: bold;\n}\n.default-button {\n    background: #2f3160 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    opacity: 1;\n    /* UI Properties */\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium)\n        var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--white);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #ffffff;\n    opacity: 1;\n    margin-left: 10px;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
